import React from 'react';
import { useSelector, useDispatch } from "react-redux";
import SliderEditBlock from './SliderEditBlock';
import { setIsModalEditOpen, editItem, setQuantityForEditRequest } from '../redux/favoriteSlice';
import { useForm } from 'react-hook-form';

function ModalEdit() {
  const text = useSelector(state => state.input.text);
  const {selectedItem, quantityForEditRequest} = useSelector(state => state.favorite);
  const id = selectedItem.id;
  const dispatch = useDispatch();

  const { register, handleSubmit } = useForm({
    defaultValues: {
      request: selectedItem ? selectedItem.request : '',
      name: selectedItem ? selectedItem.name : '',
      sortType: selectedItem ? selectedItem.sortType : '',
      quantity: quantityForEditRequest
    }
  });

  const onSubmit = (data) => {
     dispatch(editItem({
      id, data: {
        request: data.request,
        name: data.name,
        sortType: data.sortType,
        quantity: quantityForEditRequest
      }
    }));
    dispatch(setIsModalEditOpen(false));
  
  }

  const handleCloseModalEdit = () => {
    dispatch(setIsModalEditOpen(false));
  };


  return <>
    <form className='modal_edit_container' onSubmit={handleSubmit(onSubmit)}>
      <h3 className='modal_title'>  </h3>
      <div className="request_block">
        <label htmlFor="request" className='block_title'></label>
        <input className='block_input' placeholder={text} name='request' {...register('request')}  />
      </div>
      <div className="name_block">
        <label htmlFor="name" className='block_title'><span className='span_title'>*</span></label>
        <input className='block_input' placeholder=' ' name='name' {...register('name', {required: true})}   />
      </div>
      <div className="sort_block">
        <label htmlFor="sortType" className='block_title'> </label>
        <select className='block_input select' name='sortType' {...register('sortType')}  >
          <option className='selected_option'>   </option>
          <option value=" ">   </option>
          <option value="">  </option>
          <option value="">  </option>
          <option value="">  </option>
          <option value=" ">   </option>
        </select>
      </div>
      <div className="max_request_block">
          <label className='block_title' htmlFor='quantity'> </label>
          <SliderEditBlock  />
    {/*      <input type="hidden" name="quantity"  {...register('quantity')}  />*/}
      </div>
      <div className='button_block'>
        <button className='modal_button not_save' onClick={handleCloseModalEdit}> </button>
        <button className='modal_button save' type='submit'></button>
      </div>
    </form>
  </>
}

export default ModalEdit;


